* {
  box-sizing: border-box;
}

body {
  background: #e1e3de;
}

#root {
  padding: 60px;
  max-width: 800px;
  margin: auto;
  
  input {
    line-height: 2;
    font-size: 18px;
    height: auto;
    padding-left: 20px;
  }
  
  ul {
    margin: 0 -2px;
    display: flex;
    flex-wrap: wrap;
  }
  
  li {
    list-style: none;
    border: 1px solid #ccc;
    line-height: 2;
    padding: 5px 10px;
    margin: 2px;
    background: #fff;
    flex-grow: 1;
  }
  
  .labels {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -2px;
  }
  
  .label { 
    margin: 2px 2px;
    flex-grow: 1;
    
    .file { background-color: #ccc; }
  }
}
