body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box; }

body {
  background: #e1e3de; }

#root {
  padding: 60px;
  max-width: 800px;
  margin: auto; }
  #root input {
    line-height: 2;
    font-size: 18px;
    height: auto;
    padding-left: 20px; }
  #root ul {
    margin: 0 -2px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap; }
  #root li {
    list-style: none;
    border: 1px solid #ccc;
    line-height: 2;
    padding: 5px 10px;
    margin: 2px;
    background: #fff;
    -webkit-flex-grow: 1;
            flex-grow: 1; }
  #root .labels {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 0 -2px; }
  #root .label {
    margin: 2px 2px;
    -webkit-flex-grow: 1;
            flex-grow: 1; }
    #root .label .file {
      background-color: #ccc; }

